.btn {
    padding: 0.6em 1.6em;
    /* border: 2px solid #e68e1a; */
    border-radius: 20px;
    position: relative;
    overflow: hidden;
    background-color: transparent;
    text-align: center;
    text-transform: uppercase;
    font-size: 16px;
    transition: .3s;
    z-index: 1;
    font-weight: bold;
    font-family: inherit;
    /* color: #e68e1a; */
  }
  
  .btn:hover {
    border-width: 2px; /* Increase border width on hover */
    color: white; /*Change text color on hover*/
    background-color: #0a4863; /* Change background color on hover */
  }
  
  
  .active-btn {
      background-color: #0a4863; /* Change the background color on hover */
      color: white; /* Change the text color on hover */
       /* Change the border color on hover */
  }
    
  