/* Modal Container */
.base {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 999;
  }
  
  /* Modal Content */
  .modal-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    width: 50%; /* Default width for larger screens */
    max-width: 80%; /* Max width for larger screens */
    height: 80%; /* Default height for larger screens */
    max-height: 80%; /* Max height for larger screens */
    overflow-y: auto;
  }
  
  /* Media query for small screens */
  @media (max-width: 576px) {
    .modal-content {
        width: 80%; /* Adjusted width for small screens */
        max-width: 80%; /* Adjusted max width for small screens */
        height: 80vh; /* Adjusted height for small screens */
        max-height: none; /* No max height for small screens */
    }
  }
  
  /* Close Button */
  .close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
    font-size: 24px;
  }
  
  /* Modal Content Text */
  .modal-text {
    font-size: 16px;
    line-height: 1.6;
  }
  
  /* Label for additional styling */
  .label {
    font-weight: bold;
  }
  
  /* Modal Content Image */
  .modal-image {
    max-width: 180px;
    height: 270px;
  }
  